@import "../../common/_variable";
@import "../../common/_mixin";
@import "../../common/_common";

/* NHN Web Standardization Team LKJ 100118 */

/* Common */
body, p, h1, h2, h3, h4, h5, h6, ul, ol, li, dl, dt, dd, table, th, td, form, fieldset, legend, input, textarea, button {margin:0;padding:0}
body, input, textarea, select, table {font-family:"돋움", Dotum, AppleGothic, sans-serif; font-size:12px;}
table th {text-align:center;}
img, fieldset {border:0;}
hr {display:none;}
li {list-style:none;}
em, address {font-style:normal;}
a {text-decoration:none;}
a:hover, a:active, a:focus {text-decoration:underline;}
.blind {position:absolute; top:0; left:0; width:1px; height:1px; overflow:hidden; visibility:hidden; font-size:0; line-height:0;}

/* Layout */
#wrap {position:relative;}
.header_one .inner_wrap,#container,#footer,.section_tab{width:960px}
#container {position:relative; margin:25px auto 0; z-index:1; zoom:1;}
#container:after {display:block; clear:both; content:"";}
#container .snb {position:relative; float:left; width:200px; margin-right:20px; z-index:2;}
#container #content {position:relative; float:left; width:740px; padding-bottom:55px; z-index:1;}
#footer {padding-top:25px;}

